/*PostForm.css*/

.PostForm {
  display: flex;
  flex-direction: column;
  padding: 20px;
  background-color: white;
  border-radius: 10px;
  margin-bottom: 20px;
}

.PostForm__input {
  border: none;
  padding: 10px;
  outline-width: 0;
  font-size: 20px;
  margin-bottom: 10px;
}

.PostForm__options {
  display: flex;
  justify-content: space-evenly;
  padding-bottom: 20px;
}

.PostForm__option {
  padding-top: 10px;
  display: flex;
  align-items: center;
  color: gray;
  cursor: pointer;
  padding: 20px;
  border-radius: 10px;
  transition: background-color 0.15s ease;
}

.PostForm__option:hover {
  background-color: whitesmoke;
}

.PostForm__option > h3 {
  font-size: medium;
  margin-left: 10px;
}

.PostForm__option > .MuiSvgIcon-root {
  font-size: 20px;
}

.PostForm__option > .MuiSvgIcon-root {
  color: #2e81f4;
}

.PostForm__option > h3 {
  color: #2e81f4;
}

.PostForm__option:last-child {
  border-left: 1px solid lightgray;
}

.PostForm__option:last-child > h3 {
  color: gray;
}

.PostForm__option:last-child > .MuiSvgIcon-root {
  color: gray;
}

.PostForm__option:last-child:hover {
  background-color: whitesmoke;
}

.PostForm__option:last-child:hover > h3 {
  color: #2e81f4;
}

.PostForm__option:last-child:hover > .MuiSvgIcon-root {
  color: #2e81f4;
}

.PostForm__button {
  background-color: #2e81f4;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 20px;
  font-weight: 600;
  cursor: pointer;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

.PostForm__button:hover {
  background-color: #2e81f4;
}

.PostForm__button:disabled {
  background-color: lightgray;
  color: gray;
  cursor: not-allowed;
}

.PostForm__button:disabled:hover {
  background-color: lightgray;
}

.PostForm__button > .MuiSvgIcon-root {
  font-size: 20px;
  margin-right: 10px;
}

.PostForm__button > h4 {
  font-size: 15px;
}

.PostForm__button > h4:hover {
  text-decoration: underline;
}

.PostForm__button > h4:disabled {
  text-decoration: none;
}

.PostForm__button > h4:disabled:hover {
  text-decoration: none;
}

.PostForm__progress {
  width: 100%;
  margin-top: 20px;
}

.PostForm__progress > .MuiLinearProgress-root {
  height: 10px;
  border-radius: 20px;
}

.PostForm__progress > .MuiLinearProgress-colorPrimary {
  background-color: lightgray;
}

.PostForm__progress > .MuiLinearProgress-barColorPrimary {
  background-color: #2e81f4;
}

.PostForm__progress > .MuiLinearProgress-bar {
  background-color: #2e81f4;
}

.PostForm__progress > .MuiLinearProgress-bar1Buffer {
  background-color: #2e81f4;
}

.PostForm__progress > .MuiLinearProgress-bar2Buffer {
  background-color: #2e81f4;
}

.PostForm__progress > .MuiLinearProgress-dashed {
  background-image: linear-gradient(
    45deg,
    rgba(255, 255, 255, 0.15) 25%,
    transparent 25%,
    transparent 50%,
    rgba(255, 255, 255, 0.15) 50%,
    rgba(255, 255, 255, 0.15) 75%,
    transparent 75%,
    transparent
  );
  background-size: 1rem 1rem;
  animation: indeterminate 2s linear infinite;
}

