/*Home.css*/

.Home {
  display: flex;
  justify-content: space-between;
}

.Home__feed {
  flex: 0.6;
  padding: 20px;
}

.Home__feed > .Post {
  margin-bottom: 20px;
}

.Home__feed > .Post:first-child {
  margin-top: 20px;
}

.Home__feed > .Post > .Post__header {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
